export default function(t) {
    t = Object(t)
    for (var idx = 1, len = arguments.length; idx < len; idx++) {
        var n = arguments[idx]
        if (!!n) for (var k in n) if (Object.prototype.hasOwnProperty.call(n,k)) t[k] = n[k]
    }
    return t
}



