import extend from './extend'
export default opts=> {
    opts = extend({
        url:'',
        method:'get',
        body:{},
    },opts);
    return new Promise( (resolve, reject) => {

        if(opts.method == 'jsonp'){
            var cbName = 'cb_'+(Date.now()+Math.round(Math.random()*1000)).toString(32),
                doc = document,
                body = doc.body,
                node = doc.createElement('script')
            opts['body']['callback'] = cbName;
            opts['body']['t'] = Date.now();
            
            node.src = opts.url+'?'+param(opts.body)
            window[cbName] = ret => {
                window[cbName] = null
                body.removeChild(node)
                resolve(ret)
            }
            body.appendChild(node)
            return
        }

        var request = 'XMLHttpRequest' in window ? new XMLHttpRequest() : new ActiveXObject('Microsoft.XMLHTTP'),
                keys = [],
                all = [],
                headers = {};

        var response = () => ({
            ok: (request.status/100|0) == 2,        // 200-299
            statusText: request.statusText,
            status: request.status,
            url: request.responseURL,
            text: () => Promise.resolve(request.responseText),
            json: () => Promise.resolve(request.responseText).then(JSON.parse),
            blob: () => Promise.resolve(new Blob([request.response])),
            clone: response,
            headers: {
                keys: () => keys,
                entries: () => all,
                get: k => headers[k.toLowerCase()],
                has: k => k.toLowerCase() in headers
            }
        });
        
        opts.url = opts.url + '?' + ('get' == opts.method ? param(opts.body) : '')

        request.open(opts.method || 'get', opts.url, true);

        request.onload = () => {
            request.getAllResponseHeaders().replace(/^(.*?):[^\S\n]*([\s\S]*?)$/gm, (m, key, value) => {
                keys.push(key = key.toLowerCase());
                all.push([key, value]);
                headers[key] = headers[key] ? headers[key]+','+value : value;
            });
            resolve(response());
        };

        request.onerror = reject;

        request.withCredentials = opts.credentials=='include';

        for (var i in opts.headers) request.setRequestHeader(i, opts.headers[i]);

        request.send('post' == opts.method ? opts.body : ( 'get' == opts.method ? null : param(opts.body)));

    });
}
var escape = encodeURIComponent,
    param = s => {
        var r = [];
        for(var k in s) if(s.hasOwnProperty(k)) r.push(escape(k)+'='+escape(s[k]))
        r = r.join('&').replace(/%20/g, '+')
        return r || null
    }


