import xss from "xss"
const cfg = {
    onTagAttr(tag, name, val, isWhiteAttr){
        return filter(tag,name,val,isWhiteAttr)
    },
    onIgnoreTagAttr(tag, name, val, isWhiteAttr) {
        return filter(tag,name,val,isWhiteAttr)
    }
}
export default (s,isMd) => {
    if(isMd){
        cfg['whiteList'] = {
            "a": ["title", "href", "name", "target", "rel"],
            "img": ["alt", "src", "class", "referrerpolicy"]
        }
    }
    return xss(s, cfg).replace(/\<\/?div\>/ig,"");
};

const filter = (tag, name, val, isWhiteAttr)=>{
    if(/code|pre|span/i.test(tag)) {
        if(name == "style") {
            let match = val.match(/color:([#a-z0-9]{3,7}|\s+[#a-z0-9]{3,8})/gi)
            if(!!match && match.length) return 'style="'+match[0]+'"'
            return ''
        }
        if(name == 'class') return name+"='"+xss.escapeAttrValue(val)+"'"
    }
    if(tag === 'a' && name == "class" && val === 'at') return name+"='"+xss.escapeAttrValue(val)+"'"
    if (tag === 'img' && /src|class/i.test(name)) return name + "='"+xss.escapeAttrValue(val) + "' referrerPolicy='no-referrer'"
}


