import $ from '../utils'
const getApi = (appId, cb) =>{
    let url = 'https://app-router.com/2/route'
    $.ajax({
        url,
        body:{appId}
    }).then(ret=>{
        ret.json().then(r=>cb && cb(r.api_server))
    })
}
const DB = ()=> {

}

const request=(url)=> {

}

export default {
    getApi
}


